<template>
    <div class="container">
        <div class="top-nav">
            <span class="cancel" @click="goBack">返回</span>
            <span class="title">设置</span>
            <span class="reset">重置</span>
        </div>
        <div style="height: 50px;"></div>
        <div class="setting-box">
            <div class="shadow-sm bg-body-white rounded g-card">
                <div class="card-title-box">
                    <span class="card-title">应用标题</span>
                    <span class="card-secondary">展示在顶部的文字</span>
                </div>
                <div class="g-form">
                    <input type="text" class="form-control" placeholder="请输入应用标题" v-model="form.appName" @input="changeAppName">
                </div>
            </div>
            <div class="shadow-sm bg-body-white rounded g-card">
                <div class="card-title-box">
                    <span class="card-title">我的信息</span>
                </div>
                <div class="g-form">
                    <div class="form-item">
                        <span class="item-label">用户名</span>
                        <input type="text" class="form-control" placeholder="请输入用户名" v-model="form.userName" @input="changeUserName">
                    </div>
                    <div class="form-item">
                        <span class="item-label">金额</span>
                        <input type="text" class="form-control" placeholder="请输入金额" v-model="form.money" @input="changeMoney">
                    </div>
                    <div class="form-item">
                        <span class="item-label">邀请码</span>
                        <input type="text" class="form-control" placeholder="请输入邀请码" v-model="form.invitation" @input="changeInvitation">
                    </div>
                </div>
            </div>
            <div class="shadow-sm rounded g-card">
                <div class="card-title-box">
                    <span class="card-title">首页轮播图数据</span>
                </div>
                <div class="insert-btn" @click="insertCarousel">+插入一个轮播图</div>
                <div class="g-form">
                    <div class="item-panel" v-for="(item,index) in form.carousels" :key="index">
                        <span class="iconfont icon-cuowu close-btn" @click="delCarousel(index)"></span>
                        <span class="corner-code">{{ index+1 }}</span>
                        <div class="insert-content">
                            <span class="insert-content-title">轮播图</span>
                            <img class="insert-image" :src="item.url" @click="carouselImageTap(item.id)"/>
                        </div>
                    </div>
                </div>
            </div>
            <input type="file" ref="fileInput" @change="carouselImageHidden" class="d-none" />
            <div class="shadow-sm rounded g-card">
                <div class="card-title-box">
                    <span class="card-title">首页中部卡片数据</span>
                </div>
                <div class="g-form">
                    <div class="item-panel" v-for="(item,index) in form.hots" :key="index">
                        <span class="corner-code">{{ index+1 }}</span>
                        <div class="insert-content">
                            <div class="g-form zb-form">
                                <div class="form-item">
                                    <span class="item-label">标题</span>
                                    <input type="text" class="form-control" v-model="item.title" @input="changeHotTitle(index)">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">类型</span>
                                    <input type="text" class="form-control" v-model="item.type" @input="changeHotType(index)">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">金额</span>
                                    <input type="text" class="form-control" v-model="item.price" @input="changeHotPrice(index)">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">封面</span>
                                    <img :src="item.cover" @click="hotImageTap(item.id)"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="shadow-sm rounded g-card">
                <div class="card-title-box">
                    <span class="card-title">首页列表数据</span>
                </div>
                <div class="insert-btn" @click="insertList">+插入一条数据</div>
                <div class="g-form">
                    <div class="item-panel" v-for="(item,index) in form.list" :key="index">
                        <span class="iconfont icon-cuowu close-btn" @click="delList(index)"></span>
                        <span class="corner-code">{{ index+1 }}</span>
                        <div class="insert-content">
                            <div class="g-form zb-form">
                                <div class="form-item">
                                    <span class="item-label">标题</span>
                                    <input type="text" class="form-control" v-model="item.title">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">类型</span>
                                    <input type="text" class="form-control" v-model="item.type">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">金额</span>
                                    <input type="text" class="form-control" v-model="item.price">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">剩余</span>
                                    <input type="text" class="form-control" v-model="item.surplus">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">已赚</span>
                                    <input type="text" class="form-control" v-model="item.earned">
                                </div>
                                <div class="form-item">
                                    <span class="item-label">封面</span>
                                    <img :src="item.cover" @click="listImageTap(item.id)"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters,mapActions } from 'vuex'
import { swiperImage3,listImage } from '@/common/storage'
export default{
    data(){
        return{
            //1代表轮播图，2代表热点 3代表列表
            activeType:null,
            activeImage:null,
            form:{
                appName:'',
                userName:'',
                money:'',
                invitation:'',
                carousels:[],
                hots:[],
                list:[]
            },
        }
    },
    computed: {
        ...mapGetters(['appName','userName','money','invitation','carousels','hots','list']),
    },
    mounted(){
        this.form.appName = this.appName
        this.form.userName = this.userName
        this.form.money = this.money
        this.form.invitation = this.invitation
        this.form.carousels = this.carousels
        this.form.hots = this.hots
        this.form.list = this.list
    },
    methods:{
        ...mapActions(['ADD_CAROUSEL_ASYNC','UPDATE_HOT_ASYNC','UPDATE_CAROUSEL_ASYNC','DEL_CAROUSEL_ASYNC','ADD_LIST_ASYNC','UPDATE_LIST_ASYNC','DEL_LIST_ASYNC','SET_APPNAME_ASYNC','SET_USER_NAME_ASYNC','SET_MONEY_ASYNC','SET_INVITATION_ASYNC']),
        carouselImageTap(id){
            this.activeType = 1
            this.activeImage = id
            this.$refs['fileInput'].click();
        },
        hotImageTap(id){
            this.activeType = 2
            this.activeImage = id
            this.$refs['fileInput'].click();
        },
        listImageTap(id){
            this.activeType = 3
            this.activeImage = id
            this.$refs['fileInput'].click();
        },
        carouselImageHidden(event){
            let _self = this;
            const file = event.target.files[0];
            if (file) {
                console.log(file)
                const reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = (e) => {
                    console.log(e)
                    if(_self.activeType == 1){
                        let index = this.form.carousels.findIndex(obj => obj.id === _self.activeImage);
                        if(index != -1){
                            let c = _self.form.carousels[index]
                            let carouselTem = {
                                'id':c.id,
                                'url':e.target.result
                            }
                            _self.$set(_self.form.carousels, index, carouselTem);
                            _self.UPDATE_CAROUSEL_ASYNC(carouselTem)
                        }
                    }else if(_self.activeType == 2){
                        let index = this.form.hots.findIndex(obj => obj.id === _self.activeImage);
                        if(index != -1){
                            let h = _self.form.hots[index]
                            let hotTem = {
                                'id':h.id,
                                'title':h.title,
                                'type':h.type,
                                'price':h.price,
                                'cover': e.target.result
                            }
                            _self.$set(_self.form.hots, index, hotTem);
                            _self.UPDATE_HOT_ASYNC(hotTem)
                        }
                    }else if(_self.activeType == 3){
                        let index = this.form.list.findIndex(obj => obj.id === _self.activeImage);
                        if(index != -1){
                            let l = _self.form.list[index]
                            let lTem = {
                                'id':l.id,
                                'title':l.title,
                                'type':l.type,
                                'price':l.price,
                                'surplus':l.surplus,
                                'earned':l.earned,
                                'cover':e.target.result
                            }
                            _self.$set(_self.form.list, index, lTem);
                            _self.UPDATE_LIST_ASYNC(lTem)
                        }
                    }
                };
            }
        },
        goBack(){
            this.$router.go(-1);
        },
        delCarousel(index){
            this.DEL_CAROUSEL_ASYNC(index).then(()=>{
                this.form.carousels.splice(index, 1);
            })
        },
        insertCarousel(){
            let carousel = {id:Date.now(),url:swiperImage3}
            this.ADD_CAROUSEL_ASYNC(carousel).then(()=>{
                this.form.carousels.push(carousel)
            })
        },
        insertList(){
            let sj = {
                'id':Date.now(),
                'title':'父爱如山',
                'type':'短剧',
                'price':'80.00',
                'earned':15,
                'surplus':530,
                'cover':listImage
            }
            this.ADD_LIST_ASYNC(sj).then(()=>{
                this.form.list.push(sj)
            })
        },
        delList(index){
            this.DEL_LIST_ASYNC(index).then(()=>{
                this.form.list.splice(index, 1);
            })
        },
        changeAppName(res){
            this.SET_APPNAME_ASYNC(res.target.value)
        },
        changeUserName(res){
            this.SET_USER_NAME_ASYNC(res.target.value)
        },
        changeMoney(res){
            this.SET_MONEY_ASYNC(res.target.value)
        },
        changeInvitation(res){
            this.SET_INVITATION_ASYNC(res.target.value)
        },
        changeHotTitle(index){
            this.UPDATE_HOT_ASYNC(this.form.hots[index])
        },
        changeHotType(index){
            this.UPDATE_HOT_ASYNC(this.form.hots[index])
        },
        changeHotPrice(index){
            this.UPDATE_HOT_ASYNC(this.form.hots[index])
        }
    }
}
</script>
<style scoped>
.container{
    margin: 0;
    padding: 0;
    background-color: #f3f1f1;
}
.top-nav{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-bottom-color: #eee;
    color: rgb(51, 51, 51);
    background-color: rgb(255, 255, 255);
    height: 44px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    flex-direction: row;
    font-size: 12px;
}
.cancel{
    width: 60px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.title{
    flex: 1;
    text-align: center;
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.reset{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    line-height: 12px;
}
.setting-box{
    padding: 0 15px;
}
.g-card{
    background-color: #fff;
    padding: 0 15px;
    margin-top: 15px;
    
}
.card-title-box{
    display: flex;
    border-bottom: 1px #ebeef5 solid;
    flex-direction: row;
    align-items: center;
    padding: 10px;
    overflow: hidden;
}
.card-title{
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
    font-size: 14px /* line-height: 1.8; */;
}
.card-secondary{
    font-size: 12px;
    color: #909399;
}
.g-form{
    padding: 10px;
}
.form-item{
    display: flex;
    align-items: center;
}
.form-item:not(:first-child){
    margin-top: 10px;
}
.item-label{
    white-space: nowrap;
    margin-right: 10px;
    width: 60px;
    font-size: 14px;
    color: #606266;
}
.insert-btn{
    display: inline-block;
    border-radius: 6px;
    padding: 4px 6px;
    border: 1px solid #ccc;
    color: rgb(51, 51, 51);
    font-size: 14px;
    margin: 10px 0px;
}
.item-panel{
    position: relative;
    display: flex;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 0px 3px 1px;
}
.item-panel:not(:first-child){
    margin-top: 15px;
}
.close-btn{
    position: absolute;
    right: -2px;
    top: -6px;
    color: red;
    font-size: 24px;
}
.corner-code{
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    background-color: #ec602d;
    color: #fff;
}
.insert-content{
    position: relative;
    display: flex;
    width: 80%;
    height: 80%;
    margin: 10px 0;
}
.insert-content-title{
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: left;
    font-size: 14px;
    color: #606266;
    height: 36px;
    padding: 0 12px 0 0;
    vertical-align: middle;
    flex-shrink: 0;
    box-sizing: border-box;
}
.insert-image{
    width: 140px;
    height: 60px;
    object-fit: cover;
}
.form-item>img{
    width: 140px;
    height: 60px;
    object-fit: cover;
}
.zb-form>.form-item>.item-label{
    width: auto;
}
</style>